.App {
  width: 100%;
  font-family: Lato;
}

section {
  display: grid;
  gap: 20px;
  padding-inline-start: 28px;
  padding-inline-end: 28px;
  padding-top: 30px;
  padding-bottom: 25px;

  @media (min-width: 601px) {
    padding: 50px 100px;
    align-items: center;
  }
}

.icon:hover {
  opacity: .6;
  transition-timing-function: ease-in-out;
  transition: opacity .2s;
}


h3 {
  color: #1a1b1f;
  font-family: Lato;
  font-style: normal;
  margin: 0px 0 0 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 34px;
}

p {
  color: #57575C;
  font-family: Lato;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
}

h2 {
  color: #1a1b1f;
  font-family: Lato;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 34px;
  margin: 0;
  opacity: 1;
  line-height: 50px;
}

.blue-bold {
  color: #007bc2;
}

.jumbo-ish {
  font-size: 24px;
  line-height: 35px;
  margin-top: 25px;

  @media (min-width: 601px) {
    font-size: 30px;
    line-height: 50px;
  }

}

.jumbo {
    font-size: 24px;
    line-height: 35px;
    @media (min-width: 601px) {
      font-size: 36px;
      line-height: 50px;
    }
}
